.list {
  width: 380px;
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid rgb(226, 226, 226);
}

.link {
  width: 150px;
  margin: 0;
  display: inline-block;
  color: #000;
  text-decoration: none;
  padding: 20px;
  background-color: #fff;
  transition: background-color 300ms ease;
}

.link:hover {
  background-color: rgb(203, 189, 255);
}

.activeLink {
  background-color: rgb(218, 209, 255);
}

.button {
  width: 150px;
  margin: 0;
  display: inline-block;
  padding: 20px;
  background-color: #fff;
  border: 2px solid rgb(226, 226, 226);
  transition: background-color 300ms ease;
}

.button:hover {
  background-color: rgb(203, 189, 255);
}
