.section {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 40px;
  width: 1000px;
  margin-left: auto;
  margin-right: auto;
  border: 2px solid rgb(233, 233, 233);
  border-radius: 3px;
}

.image {
  display: block;
  width: 500px;
}

.container {
  width: 500px;
  text-align: left;
  padding: 50px;
}

.subtitle {
  font-weight: 600;
}
