.list {
  width: 1200px;
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 10px;
  margin-top: 20px;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
}

.item {
  margin: 0;
  display: block;
  padding: 20px 40px;
  border: 2px solid rgb(20, 0, 107);
  border-radius: 3px;
  background-color: #fff;
  transition: background-color 300ms ease;
}
